.Home {
    display: flex;
    justify-content: center;
}

.home-box {
    width: fit-content;
    margin-top: 1.5cm;
}

.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    background: hsla(302, 82%, 76%, 1);
    background: linear-gradient(90deg, hsla(302, 82%, 76%, 1) 0%, hsla(258, 40%, 68%, 1) 100%);

    // background: hsla(352, 83%, 64%, 1); for dark mode
    // background: linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%); for dark mode
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}